<template><undefined>
<h1>Input 输入框</h1>
<p>通过鼠标或键盘输入内容，是最基础的表单域的包装。</p>
<h2 id="he-shi-shi-yong"><a class="header-anchor" href="#he-shi-shi-yong" aria-hidden="true">¶</a> 何时使用</h2>
<ul>
<li>需要用户输入表单域内容时。</li>
<li>提供组合型输入框，带搜索的输入框，还可以进行大小选择。</li>
</ul>
<h2 id="zu-jian-yan-shi"><a class="header-anchor" href="#zu-jian-yan-shi" aria-hidden="true">¶</a> 组件演示</h2>
<demo-box :jsfiddle="{&quot;html&quot;:&quot;&lt;template&gt;\n  &lt;v-input placeholder=\&quot;基本使用\&quot; v-model=\&quot;value\&quot; @keydown=\&quot;keydown\&quot; @keyup=\&quot;keyup\&quot; @blur=\&quot;blur('blur')\&quot;&gt;&lt;/v-input&gt;\n&lt;/template&gt;\n\n&quot;,&quot;script&quot;:&quot;\nexport default {\n    methods: {\n        blur(val) {\n            console.log(val)\n        },\n        keyup(val){\n            console.log(val)\n        },\n        keydown(val){\n            console.log(val)\n        },\n\n    }\n}\n&quot;,&quot;style&quot;:null}">
                  <div class="box-demo-show" slot="component"><template>
  <v-input placeholder="基本使用" v-model="value" @keydown="keydown" @keyup="keyup" @blur="blur('blur')"></v-input>
</template>

</div>
                  <div slot="description"><h4 id="ji-ben"><a class="header-anchor" href="#ji-ben" aria-hidden="true">¶</a> 基本</h4>
<p>基本使用。</p>
</div>
                  <div class="highlight" slot="code"><pre><code class="hljs language-html"><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">v-input</span> <span class="hljs-attr">placeholder</span>=<span class="hljs-string">"基本使用"</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"value"</span> <span class="hljs-attr">@keydown</span>=<span class="hljs-string">"keydown"</span> <span class="hljs-attr">@keyup</span>=<span class="hljs-string">"keyup"</span>  <span class="hljs-attr">@blur</span>=<span class="hljs-string">"blur('blur')"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">v-input</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
<span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
    <span class="hljs-attr">methods</span>: {
        blur(val) {
            <span class="hljs-built_in">console</span>.log(val)
        },
        keyup(val){
            <span class="hljs-built_in">console</span>.log(val)
        },
        keydown(val){
            <span class="hljs-built_in">console</span>.log(val)
        },

    }
}
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
</div>
