<demo-box :jsfiddle="{&quot;html&quot;:&quot;  &lt;v-input type=\&quot;textarea\&quot; placeholder=\&quot;Autosize height based on content lines\&quot; autosize&gt;&lt;/v-input&gt;\n  &lt;br&gt;&lt;br&gt;\n  &lt;v-input type=\&quot;textarea\&quot; placeholder=\&quot;Autosize height with minimum and maximum number of lines\&quot; :autosize=\&quot;{ minRows: 2, maxRows: 6 }\&quot;&gt;&lt;/v-input&gt;\n&quot;,&quot;script&quot;:null,&quot;style&quot;:null}">
