                  <div slot="description"><h4 id="gua-ying-wen-ben-gao-du-de-wen-ben-yu"><a class="header-anchor" href="#gua-ying-wen-ben-gao-du-de-wen-ben-yu" aria-hidden="true">¶</a> 适应文本高度的文本域</h4>
<p><code>autosize</code> 属性适用于 <code>textarea</code> 节点，并且只有高度会自动变化。另外 <code>autosize</code> 可以设定为一个对象，指定最小行数和最大行数。</p>
</div>
                  <div class="highlight" slot="code"><pre><code class="hljs language-html">  <span class="hljs-tag">&lt;<span class="hljs-name">v-input</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"textarea"</span> <span class="hljs-attr">placeholder</span>=<span class="hljs-string">"Autosize height based on content lines"</span> <span class="hljs-attr">autosize</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">v-input</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">br</span>&gt;</span><span class="hljs-tag">&lt;<span class="hljs-name">br</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">v-input</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"textarea"</span> <span class="hljs-attr">placeholder</span>=<span class="hljs-string">"Autosize height with minimum and maximum number of lines"</span> <span class="hljs-attr">:autosize</span>=<span class="hljs-string">"{ minRows: 2, maxRows: 6 }"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">v-input</span>&gt;</span>
</code></pre>
</div>
                <summary>
