  `autosize` 属性适用于 `textarea` 节点，并且只有高度会自动变化。另外 `autosize` 可以设定为一个对象，指定最小行数和最大行数。
</summary>
<pre v-pre=""><code v-pre="" class="hljs language-html">  <span class="hljs-tag">&lt;<span class="hljs-name">v-input</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"textarea"</span> <span class="hljs-attr">placeholder</span>=<span class="hljs-string">"Autosize height based on content lines"</span> <span class="hljs-attr">autosize</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">v-input</span>&gt;</span>
