<demo-box :jsfiddle="{&quot;html&quot;:&quot;&lt;template&gt;\n  &lt;v-input&gt;\n      &lt;span slot=\&quot;before\&quot;&gt;http://&lt;/span&gt;\n      &lt;span slot=\&quot;after\&quot;&gt;.com&lt;/span&gt;\n  &lt;/v-input&gt;\n  &lt;br&gt;\n  &lt;v-input&gt;\n      &lt;v-select placeholder=\&quot;请选择\&quot; style=\&quot;width: 80px;\&quot; slot=\&quot;before\&quot; :data=\&quot;options\&quot; v-model=\&quot;value\&quot;&gt;&lt;/v-select&gt;\n      &lt;v-select style=\&quot;width: 80px;\&quot; slot=\&quot;after\&quot; :data=\&quot;options2\&quot; v-model=\&quot;value\&quot;&gt;&lt;/v-select&gt;\n  &lt;/v-input&gt;\n&lt;/template&gt;\n\n&quot;,&quot;script&quot;:&quot;\nexport default {\n    data() {\n        return {\n            options: [{value: '1', label: 'lady'}, {value: '2', label: '小强', disabled: true}, {value: '3', label: '小明'}],\n            options2: [{value: '1', label: 'lady'}, {value: '2', label: '小强'}, {value: '3', label: '小明'}],\n            value: '3'\n        }\n    }\n}\n&quot;,&quot;style&quot;:null}">
                  <div class="box-demo-show" slot="component"><template>
  <v-input>
      <span slot="before">http://</span>
      <span slot="after">.com</span>
  </v-input>
  <br>
  <v-input>
      <v-select placeholder="请选择" style="width: 80px;" slot="before" :data="options" v-model="value"></v-select>
      <v-select style="width: 80px;" slot="after" :data="options2" v-model="value"></v-select>
  </v-input>
</template>

</div>
                  <div slot="description"><h4 id="qian-hou-zhui-xiu-shi-tian-jia"><a class="header-anchor" href="#qian-hou-zhui-xiu-shi-tian-jia" aria-hidden="true">¶</a> 前后缀修饰添加</h4>
<p>带有前缀后缀修饰</p>
</div>
                  <div class="highlight" slot="code"><pre><code class="hljs language-html"><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">v-input</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">span</span> <span class="hljs-attr">slot</span>=<span class="hljs-string">"before"</span>&gt;</span>http://<span class="hljs-tag">&lt;/<span class="hljs-name">span</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">span</span> <span class="hljs-attr">slot</span>=<span class="hljs-string">"after"</span>&gt;</span>.com<span class="hljs-tag">&lt;/<span class="hljs-name">span</span>&gt;</span>
  <span class="hljs-tag">&lt;/<span class="hljs-name">v-input</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">br</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">v-input</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">v-select</span> <span class="hljs-attr">placeholder</span>=<span class="hljs-string">"请选择"</span> <span class="hljs-attr">style</span>=<span class="hljs-string">"width: 80px;"</span> <span class="hljs-attr">slot</span>=<span class="hljs-string">"before"</span> <span class="hljs-attr">:data</span>=<span class="hljs-string">"options"</span>
      <span class="hljs-attr">v-model</span>=<span class="hljs-string">"value"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">v-select</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">v-select</span> <span class="hljs-attr">style</span>=<span class="hljs-string">"width: 80px;"</span> <span class="hljs-attr">slot</span>=<span class="hljs-string">"after"</span> <span class="hljs-attr">:data</span>=<span class="hljs-string">"options2"</span>
      <span class="hljs-attr">v-model</span>=<span class="hljs-string">"value"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">v-select</span>&gt;</span>
  <span class="hljs-tag">&lt;/<span class="hljs-name">v-input</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
<span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
    data() {
        <span class="hljs-keyword">return</span> {
            <span class="hljs-attr">options</span>: [{<span class="hljs-attr">value</span>: <span class="hljs-string">'1'</span>, <span class="hljs-attr">label</span>: <span class="hljs-string">'lady'</span>}, {<span class="hljs-attr">value</span>: <span class="hljs-string">'2'</span>, <span class="hljs-attr">label</span>: <span class="hljs-string">'小强'</span>, <span class="hljs-attr">disabled</span>: <span class="hljs-literal">true</span>}, {<span class="hljs-attr">value</span>: <span class="hljs-string">'3'</span>, <span class="hljs-attr">label</span>: <span class="hljs-string">'小明'</span>}],
            <span class="hljs-attr">options2</span>: [{<span class="hljs-attr">value</span>: <span class="hljs-string">'1'</span>, <span class="hljs-attr">label</span>: <span class="hljs-string">'lady'</span>}, {<span class="hljs-attr">value</span>: <span class="hljs-string">'2'</span>, <span class="hljs-attr">label</span>: <span class="hljs-string">'小强'</span>}, {<span class="hljs-attr">value</span>: <span class="hljs-string">'3'</span>, <span class="hljs-attr">label</span>: <span class="hljs-string">'小明'</span>}],
            <span class="hljs-attr">value</span>: <span class="hljs-string">'3'</span>
        }
    }
}
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
</div>
