<pre v-pre=""><code v-pre="" class="hljs language-html">  <span class="hljs-tag">&lt;<span class="hljs-name">v-input</span> <span class="hljs-attr">size</span>=<span class="hljs-string">"large"</span> <span class="hljs-attr">placeholder</span>=<span class="hljs-string">"大尺寸"</span> <span class="hljs-attr">style</span>=<span class="hljs-string">"width:200px;display:inline-block"</span> <span class="hljs-attr">disabled</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">v-input</span>&gt;</span>
