<h2 id="api"><a class="header-anchor" href="#api" aria-hidden="true">¶</a> API</h2>
<h3 id="input-props"><a class="header-anchor" href="#input-props" aria-hidden="true">¶</a> Input Props</h3>
<table class="table"><thead>
<tr>
<th>参数</th>
<th>说明</th>
<th>类型</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>type</td>
<td>【必须】声明 input 类型，同原生 input 标签的 type 属性。另外提供 type=&quot;textarea&quot;。</td>
<td>String</td>
<td>text</td>
</tr>
<tr>
<td>id</td>
<td>输入框的 id</td>
<td>String</td>
<td>-</td>
</tr>
<tr>
<td>placeholder</td>
<td>选择框默认文字</td>
<td>String</td>
<td>0</td>
</tr>
<tr>
<td>value</td>
<td>value 值</td>
<td>any</td>
<td>-</td>
</tr>
<tr>
<td>size</td>
<td>控件大小，默认值为 default 。{&quot;large&quot;,&quot;default&quot;,&quot;small&quot;}注：标准表单内的输入框大小限制为 large。</td>
<td>String</td>
<td>default</td>
</tr>
<tr>
<td>disabled</td>
<td>是否禁用状态，默认为 false</td>
<td>Boolean</td>
<td>false</td>
</tr>
<tr>
<td>autosize</td>
<td>自适应内容高度，可设置为 <code v-pre="">true|false</code> 或对象：<code v-pre="">{ minRows: 2, maxRows: 6 }</code>,仅在type为textarea时生效</td>
<td>Boolean/Object</td>
<td>false</td>
</tr>
<tr>
<td>debounce</td>
<td>延迟输入状态变化的时间（ms）</td>
<td>Number</td>
<td>0</td>
</tr>
<tr>
<td>slot:before</td>
<td>input前面加前缀修饰</td>
<td>slot node</td>
<td>-</td>
</tr>
<tr>
<td>slot:after</td>
<td>input后面加后缀修饰</td>
<td>slot node</td>
<td>-</td>
</tr>
</tbody>
</table>
<h3 id="input-events"><a class="header-anchor" href="#input-events" aria-hidden="true">¶</a> Input Events</h3>
<table class="table"><thead>
<tr>
<th>事件</th>
<th>说明</th>
<th>参数</th>
</tr>
</thead>
<tbody>
<tr>
<td>blur</td>
<td>blur事件</td>
<td>val</td>
</tr>
<tr>
<td>keyup</td>
<td>keyup事件</td>
<td>val</td>
</tr>
<tr>
<td>keydown</td>
<td>keydown事件</td>
<td>val</td>
</tr>
</tbody>
</table>
<h3 id="inputgroup"><a class="header-anchor" href="#inputgroup" aria-hidden="true">¶</a> InputGroup</h3>
<table class="table"><thead>
<tr>
<th>参数</th>
<th>说明</th>
<th>类型</th>
