</tr>
</thead>
<tbody>
<tr>
<td>size</td>
<td>InputGroup 中所有的 Input 的大小，可选 large default small。</td>
<td>String</td>
<td>default</td>
</tr>
<tr>
<td>compact</td>
<td>是否用紧凑模式。</td>
<td>Boolean</td>
<td>false</td>
</tr>
</tbody>
</table>
</undefined></template>

<script>
export default {
    data() {
        return {
            options: [{
                value: '1',
                label: 'lady'
            }, {
                value: '2',
                label: '小强',
                disabled: true
            }, {
                value: '3',
                label: '小明'
            }],
            options2: [{
                value: '1',
                label: 'lady'
            }, {
                value: '2',
                label: '小强'
            }, {
                value: '3',
                label: '小明'
            }],
            value: '1'
        }
    },
    methods: {
        blur(val) {
            console.log(val)
        },
        keyup(val){
            console.log(val)
        },
        keydown(val){
            console.log(val)
        },
    }
}
</script>


